minispade.register('help', function(){
/** @jsx React.DOM */
Help = React.createClass({displayName: 'Help',
    getInitialState: function(){
        return {visible: false};
    },
    handleClick: function(event){
        this.setState({visible: !this.state.visible});
    },
    render: function(){
        var helpStyle = {
            right: "110px",
            left: "auto",
            top: "40px",
            display: this.state.visible ? "block" : "none"
        };
        return React.DOM.div(null, 
                 React.DOM.img( {className:"cowboy", src:"/images/cowboy.png", onClick:this.handleClick} ),
                 React.DOM.div( {id:"help", className:"popover left", style:helpStyle}, 
                   React.DOM.div( {className:"arrow", style:{top: 48}} ),
                     React.DOM.h3( {className:"popover-title"}, this.props.content.title),
                     React.DOM.div( {className:"popover-content"}, this.props.content.body)
                  )
              );
    }
});


Help.projects = {
    title: "Welcome to GiddyUp!",
    body: React.DOM.div(null, React.DOM.p( {key:"projects1"}, "GiddyUp is an API and user-interface to test results we collect"+' '+
                   "from ", React.DOM.code(null, "riak_test"),". I'm your guide, ", React.DOM.em(null, "Artie"),", the"+' '+
                   "Lone Testing Ranger."),
                React.DOM.p( {key:"projects2"}, "To get started, click a project to see which versions we've wrangled."))
};

Help.project = {
  title: "Yeehaw!",
  body: React.DOM.p(null, "These are all the versions we've tested on the project, which I"+' '+
            "reckon are called scorecards. Click one in the second row to see a"+' '+
            "matrix of its results.")
};

Help.scorecard = {
  title: "Lookout, stampede!",
  body: React.DOM.div(null, 
  React.DOM.p(null, "Y'er looking at the results of test runs for a single project"+' '+
"version."),

React.DOM.p(null, "Down the leftmost column are the test names that were run. Across"+' '+
  "the top are the platforms the tests were run against."),

React.DOM.p(null, "The colored bubbles indicate whether the latest run was a ", React.DOM.span(
   {className:"badge badge-success"}, "pass"), " or a ", React.DOM.span( {className:"badge"+' '+
   "badge-important"}, "failure"),". Tests that have no results"+' '+
   "recorded are ", React.DOM.span( {className:"badge"}, "gray"),". While we're still"+' '+
   "counting the steers, the bubble will be ", React.DOM.span( {className:"badge"+' '+
   "badge-loading"}, React.DOM.a(null, "outlined")),"."),

React.DOM.p(null, "The text in the bubble tells ye' the parameters for the"+' '+
"test:"),

React.DOM.ul(null, 
  React.DOM.li(null,  " ", React.DOM.span( {className:"badge"}, "U"), " Default backend"),
  React.DOM.li(null,  " ", React.DOM.span( {className:"badge"}, "B"), " Bitcask backend"),
  React.DOM.li(null,  " ", React.DOM.span( {className:"badge"}, "L"), " LevelDB backend"),
  React.DOM.li(null,  " ", React.DOM.span( {className:"badge"}, "M"), " Memory backend"),
  React.DOM.li(null,  " ", React.DOM.span( {className:"badge"}, "N"), " Multi-backend"),
  React.DOM.li(null,  " ", React.DOM.span( {className:"badge"}, "-1"), " Upgraded from previous"),
  React.DOM.li(null,  " ", React.DOM.span( {className:"badge"}, "-2"), " Upgraded from legacy")
),

React.DOM.p(null, "Ye' can click a bubble to see the results we got in the corral."))

};

Help.test_instance = {
  title: "Have a look-see",
  body: React.DOM.div(null,   "  ",  React.DOM.p(null, "We herded these here test results into the corral for ye'."),

  React.DOM.p(null, "Down the left side are all the results, whether they be good or"+' '+
    "bad, how old they are, and which brood ", React.DOM.em(null, "erm"), " version they"+' '+
    "came from. Select one and ye'll see its output on the right."))

};

Help.test_result = {
  title: "Git along, lil' dogie!",
  body: React.DOM.p(null, "We herded a number for ye' from the time that there test was run."+' '+
  "Click an artifact to get an ideer of how things transpired.")
};

Help.artifact = {
  title: "Whoa, boy!",
  body: React.DOM.div(null,   "  ",  React.DOM.p(null, "That there is a lot of stuff! riak_test uses lots of four-dollar"+' '+
  "words, I tell ye'."),

  React.DOM.p(null, "Scroll down to see the rest of the output if the thing you've"+' '+
  "selected is text. Otherwise just have a look at the pretty"+' '+
  "picture!"))
};
});
