minispade.register('projects', function(){
/** @jsx React.DOM */

ProjectsNav = React.createClass({displayName: 'ProjectsNav',
    getInitialState: function(){
        return { projects: GiddyUp.projects };
    },
    componentWillMount: function(){
        var self = this;
        GiddyUp.fetchProjects(function(projects){
            self.setState({
                projects: projects
            });
        });
    },
    render: function() {
        var self = this;
        var projects = this.state.projects.map(function(project) {
            var key = project.name;
            return ProjectNavLink( {key:key, project:project, showing:self.props.showing});
        });
        return (
                React.DOM.div( {className:"navbar", style:{marginBottom: 0}} , 
                  React.DOM.div( {className:"navbar-inner"}, 
                    React.DOM.a( {href:"#/", className:"brand"}, "GiddyUp"),
                    React.DOM.ul( {className:"nav"}, projects)
                  )
                )
        );
    }
});

ProjectNavLink = React.createClass({displayName: 'ProjectNavLink',
    render: function(){
        var url = '#' + routie.lookup('project', {project_id: this.props.project.name});
        return (React.DOM.li( {className:(this.props.showing.project_id ===
                                this.props.project.name) ? "active" : ""}, 
                  React.DOM.a( {href:url},  " ", this.props.project.name, " " )
                ));
    }
});
});
