minispade.register('loading', function(){
/** @jsx React.DOM */
Loading = React.createClass({displayName: 'Loading',
    render: function(){
        var queue = this.props.queue,
            length = Object.keys(queue).length,
            statusStyle = {
                right: "10px",
                left: "auto",
                top: "140px",
                display: (length > 0) ? "block" : "none"
            },
            requests = Object.keys(queue).map(function(k){
                var req = queue[k];
                return (React.DOM.li( {key:req.id},  " - ", req.helpText, " " ));
            });
        return (
            React.DOM.div( {className:"popover bottom", style:statusStyle}, 
              React.DOM.div( {className:"arrow", style:{left: "75%"}}),
              React.DOM.h3( {className:"popover-title"}, "Hold yer horses!"),
              React.DOM.div( {className:"popover-content"}, 
                React.DOM.p(null, "I've got ", React.DOM.span( {className:"text-info"},  " ", length, " " ),
                "more steers to herd."),
                React.DOM.ul( {className:"unstyled"},  " ",  requests,  " " )
              )
            )
        );
    }
});
});
